import Vue from 'vue'
import Router from 'vue-router'
import BaseTheme from './BaseTheme/Theme'
import WebsitePages from './pages';

Vue.use(Router)

const WebsiteConfig = {
  pages: WebsitePages,

  newRouter (routes) {
    return new Router({
      mode: 'history',
      base: process.env.BASE_URL,
      routes: BaseTheme.newRouter(routes)
    });
  }
}

export default WebsiteConfig;