import axios from 'axios'

const core = {
  install (Vue, options = {}) {

    Vue.prototype.$get_properties = async function (propertyID = '') {
      try {
        const response = await axios.get(`https://my-json-server.typicode.com/tBoccinfuso/demo/properties/${propertyID}`)
        return response.data
      } catch (err) {
        return 'unable to get data :('
      }
    }

    console.log('Core plugin installed')
  },

  buildRoutes (themeRoutes, websitesRoutes = {}) {
    let newRoutes = [],
        tempRoutes = [];

    for (const [key, route] of Object.entries(themeRoutes)) {
      tempRoutes.push(route);
    }

    for (const [key, route] of Object.entries(websitesRoutes)) {
      newRoutes.push(route);

      for (let i = 0; i < tempRoutes.length; i++) {
        if (!newRoutes.includes(tempRoutes[i])) {
          newRoutes.push(tempRoutes[i])
        }
      }
    }

    console.log('Routes built by the Core')
    return newRoutes;
  }
};

export default core;