import core from './FrontendCore/core'
import ThemePages from './config/BaseThemePages'

const Theme = {
  pages: ThemePages,

  install (Vue, options = {}) {
    // Install the core
    core.install(Vue, options);

    // Add $mosaic as a property on the Vue instance (this.$mosaic)
    Vue.prototype.$mosaic = {
      hello: () => {
        return 'Hello from Mosaic'
      }
    }

    console.log('Mosaic (theme) plugin installed')
  },

  newRouter(websiteRoutes) {
    return core.buildRoutes(ThemePages, websiteRoutes)
  }

}

export default Theme;