import Vue from 'vue'
import App from './App.vue'
import Theme from './BaseTheme/Theme'
import WebsiteConfig from './config'

Vue.config.productionTip = false

Vue.use(Theme)

// Merge the Website & Theme pages into one router.
const router = WebsiteConfig.newRouter(WebsiteConfig.pages);

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
