const ThemePages = {
  home: {
    path: '/',
    name: 'baseHome',
    component: () => import('../Pages/Home/Home.vue')
  },
  about: {
    path: 'm-about',
    name: 'baseAbout',
    component: () => import('../Pages/About/About.vue'),

    children: [
       {
        path: 'team',
        name: 'mosaicTeamBios',
        component: () => import('../Pages/TeamBios/TeamBios.vue')
      }
    ]
  },
  error: {
    path: '*',
    name: 'baseError',
    redirect: '404'
  },
  404: {
    path: '404',
    name: 'baseNotFound',
    component: () => import('../Pages/404.vue')
  }
};

export default ThemePages;